export default "<template>\n  <bean-table\n    :columns=\"columns\"\n    :value=\"value\"\n    :actions=\"actions\"\n    :actionColumnProps=\"actionColumnProps\"\n    @cell-click=\"handleCellClick\"\n  />\n</template>\n\n<script>\nimport { Vue, Component } from 'vue-property-decorator';\n\n@Component\nexport default class Example extends Vue {\n  value = [\n    {\n      id: 1,\n      province: '',\n      city: '',\n      district: '',\n      created_at: '2021-11-17 11:33:04',\n      updated_at: '2021-11-17 11:33:04',\n    },\n    {\n      id: 2,\n      province: '',\n      city: '',\n      district: '',\n      created_at: '2021-11-17 11:33:04',\n      updated_at: '2021-11-17 11:33:04'\n    }\n  ];\n\n  columns = [\n    // prop  https://element.eleme.cn/#/zh-CN/component/table#table-column-attributes\n    {\n      prop: 'province',\n      label: '',\n    },\n    {\n      prop: 'city',\n      label: '',\n    },\n    {\n      prop: 'district',\n      label: ''\n    },\n    {\n      prop: 'created_at',\n      label: '',\n      renderCell: 'time',\n      minWidth: 100,\n    },\n    {\n      prop: 'updated_at',\n      label: '',\n      minWidth: 150,\n      renderCell: (h, { props: { value } }) => {\n        return <span>:{value}</span>\n      }\n    }\n  ]\n\n  actionColumnProps = {\n    label: '',\n    // prop  https://element.eleme.cn/#/zh-CN/component/table#table-column-attributes\n  }\n\n  actions(scope) {\n    return [\n      {\n        component: 'confirmButton',\n        props: {\n          label: `${scope.row.province}`,\n          type: 'danger',\n        },\n        on: {\n          confirm: () => {\n            alert('');\n          }\n        }\n      },\n      {\n        component: () => <el-button type=\"warning\" onClick={() => alert('')}></el-button>\n      }\n    ]\n  }\n\n  handleCellClick() {\n    alert('cell click');\n  }\n\n}\n</script>\n";