<template>
  <bean-source-page
    resource="topics"
    type="new"
    title="Topics"
    :formColumns="formColumns"
    :onFormSubmit="onFormSubmit"
  />
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import _ from 'lodash';

const TABS = {
  ask: '',
  share: '',
  job: '',
  good: ''
}

const CustomInput = {
  template: `
    <div>
      <p>CustomInput</p>
      <el-input :value="value" @input="$emit('change', $event)" />
    </div>
  `,
  props: {
    value: {
      type: String,
      default: ''
    }
  },
}

@Component
export default class TopicsForm extends Vue {

  formColumns(h, { data, change }) {
    return [
      {
        prop: 'title',
        label: '',
        renderCell: {
          component: 'input',
          required: true
        }
      },
      {
        prop: 'tab',
        label: '',
        minWidth: 120,
        renderCell: {
          component: 'select',
          required: true,
          props: {
            options: _.map(TABS, (label, value) => ({
              label,
              value
            }))
          },
          on: {
            change(value) {
              // title
              change({ title: `[${value}]${data.title || ''}` })
            }
          }
        }
      },
      {
        prop: 'customInput',
        label: '',
        renderCell: {
          // value props, change
          component: (h, context) => <CustomInput {...context} />
        }
      }
    ]
  }

  onFormSubmit({ data }) {
    alert(JSON.stringify(data, null, 2));
  }
}

export const route = [
  {
    path: '/topics-form',
    name: 'topics-form',
    component: TopicsForm
  },
]
</script>

