<template>
  <bean-source-page
    resource="topics"
    type="show"
    title="Topics"
    :tabs="tabs"
    :onFetchData="onFetchData"
    :actions="actions"
  />
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import _ from 'lodash';

const TABS = {
  ask: '',
  share: '',
  job: '',
  good: ''
}

@Component
export default class TopicsDetail extends Vue {

  async onFetchData() {
    const baseURL = 'https://cnodejs.org/api/v1';
    const { data } = await this.$request.get(`/topic/${this.$route.params.id}`, {}, { baseURL });
    return { data };
  }

  tabs = [
    {
      label: '',
      columns: [
      {
        prop: 'id',
        label: 'ID',
        minWidth: 120
      },
      {
        prop: 'title',
        label: '',
        minWidth: 150
      },
      {
        prop: 'tab',
        label: '',
        minWidth: 120,
        renderCell: (h, { props: { value } }) => {
          return <el-tag>{ TABS[value] }</el-tag>
        }
      },
      {
        prop: 'visit_count',
        label: '',
        minWidth: 100
      },
      {
        prop: 'reply_count',
        label: '',
        minWidth: 100
      },
      {
        prop: 'author.avatar_url',
        label: '',
        renderCell: (h, { props: { value } }) => {
          return <el-image src={value} />
        },
        minWidth: 100
      },
      {
        prop: 'author',
        label: '',
        minWidth: 120,
        renderCell: (h, { props: { scope } }) => {
          return <span>{_.get(scope.row, 'author.loginname')}</span>
        },
      },
      {
        prop: 'good',
        label: '',
        renderCell: 'bool',
        minWidth: 80
      },
      {
        prop: 'top',
        label: '',
        renderCell: 'bool',
        minWidth: 80
      },
      {
        prop: 'create_at',
        label: '',
        renderCell: 'time',
        minWidth: 150
      },
      {
        prop: 'last_reply_at',
        label: '',
        renderCell: 'time',
        minWidth: 150
      },
      ]
    },
    {
      label: 'tab1',
      renderCell: (h, value) => {
        return <pre>{JSON.stringify(value, null, 2)}</pre>
      }
    }
  ];

  actions() {
    return [];
  }
}

export const route = [
  {
    path: '/topics-tab/:id',
    name: 'topics-tab.show',
    component: TopicsDetail
  },
]
</script>

